<template>
  <div class="row">
    <modal :show="modalStatus">
      <div class="d-flex align-items-center gap-4">
        <h6 class="modal-title" id="modal-title-default">Edit Admin Info</h6>
        <div v-if="getOrganizerInProgress.loading" class="spinner-border spinner-border-sm" role="status"></div>
      </div>

      <p class="mt-4">change these data in order to update <strong>Admin</strong> information</p>

      <form @submit.prevent="">
        <div class="d-flex flex-column">
          <input-component
            @input="name"
            type="text"
            placeholder="name"
            name="name"
            size="md"
            :value="form.name ?? getOrganizerDetails.name"
            :valid="formValidation.name.isValid"
            :startValidate="formValidation.name.startValidate"
            :errorMessage="formValidation.name.errorMessage"
          />
          <input-component
            @input="slogan"
            type="text"
            placeholder="slogan"
            name="slogan"
            size="md"
            :value="form.slogan ?? getOrganizerDetails.slogan"
          />
          <input-component
            @input="description"
            type="text"
            placeholder="description"
            name="description"
            size="md"
            :value="form.description ?? getOrganizerDetails.description"
          />

          <CheckBoxComponent @input="isDisabled" :id="'add-admin-isDisabled'">Is Disabled</CheckBoxComponent>
        </div>

        <div class="d-flex justify-content-between gap-1">
          <button-component
            class="mt-4"
            variant="gradient"
            color="success"
            size="sm"
            :isLoading="actionInProgress.loading"
            :disabled="actionInProgress.loading || getOrganizerInProgress.loading || formStatus"
            @click="editOrganizer()"
          >
            Submit
          </button-component>
          <button-component class="mt-4" variant="gradient" color="danger" size="sm" @click="toggleModal()">
            Close
          </button-component>
        </div>
      </form>
    </modal>
  </div>
</template>

<script>
  import Modal from '@/components/Modal-dialog';
  import ButtonComponent from '@/components/Button.vue';
  import InputComponent from '@/components/Input.vue';
  import CheckBoxComponent from '@/components/Checkbox';

  export default {
    name: 'edit-organizer',
    components: { Modal, ButtonComponent, InputComponent, CheckBoxComponent },
    data() {
      return {
        form: {
          id: null,
          name: null,
          slogan: null,
          description: null,
          isDisabled: false,
          imageUrl: '',
        },
        formValidation: {
          name: { isValid: false, startValidate: false, errorMessage: '' },
        },
      };
    },
    computed: {
      modalStatus() {
        return this.$store.state.OrganizersStore.modals.editOrganizer;
      },
      actionInProgress() {
        return this.$store.state.OrganizersStore.progress.updateOrganizers;
      },
      getOrganizerInProgress() {
        return this.$store.state.OrganizersStore.progress.getOrganizers;
      },
      getOrganizerDetails() {
        return this.$store.state.OrganizersStore.organizerDetails;
      },
      formStatus() {
        return this.formValidation.name.isValid;
      },
    },

    mounted() {
      this.$store.dispatch('OrganizersStore/getOrganizer');
    },

    methods: {
      name(event) {
        this.form.name = event.target.value;
      },

      slogan(event) {
        this.form.slogan = event.target.value;
      },

      description(event) {
        this.form.description = event.target.value;
      },

      isDisabled(event) {
        this.form.isDisabled = event.target.checked;
      },

      toggleModal() {
        this.$store.commit('OrganizersStore/TOGGLE_EDIT_ORGANIZER_MODAL');
      },

      startValidation() {
        if (!this.form.name) {
          this.form.name = this.getOrganizerDetails.name;
        }
        if (!this.form.description) {
          this.form.description = this.getOrganizerDetails.description;
        }
        if (!this.form.slogan) {
          this.form.slogan = this.getOrganizerDetails.slogan;
        }
        this.form.id = this.getOrganizerDetails.id;
        this.formValidation.name.startValidate = true;
      },

      checkForm() {
        this.startValidation();
        if (!this.form.name.length) {
          this.formValidation.name.isValid = false;
          this.formValidation.name.errorMessage = 'Name Field is required';
        } else {
          this.formValidation.name.isValid = true;
        }
      },
      editOrganizer() {
        this.checkForm();
        if (this.formStatus) {
          this.$store.dispatch('OrganizersStore/updateOrganizer', this.form);
        }
      },
    },
  };
</script>
